  
.cactuses {
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  animation: list-animation 1s ease-out forwards;
}

.cactuses__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@keyframes list-animation {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}