@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #929292;
}


.wrapper {
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 14px;
  background-color: white;
}
.card {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.card__wrapper {
  display: flex;
}

.card__title {
  margin: 0 0 0.25rem 0;
}

.card__description {
  font-style: italic;
}

.card__price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #00ac5c;
  font-size: 1.25rem;
}

.card__image {
  width: 80px;
  height: 90px;
  margin-right: 20px;
}
.form__input {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.form__label {
  font-weight: bold;
  margin-right: 1rem;
}

.form__item {
  width: 3rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font: inherit;
  padding-left: 0.5rem;
}
.form {
  text-align: right;
}

.form__button {
  font: inherit;
  cursor: pointer;
  background-color: #009e4f;
  border: 1px solid #009e4f;
  color: white;
  padding: 0.25rem 2rem;
  border-radius: 20px;
  font-weight: bold;
}

.form__button:hover,
.form__button:active {
  background-color: #197446;
  border-color: #197446;
}
  
.cactuses {
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  animation: list-animation 1s ease-out forwards;
}

.cactuses__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@keyframes list-animation {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.button {
  cursor: pointer;
  font: inherit;
  border: none;
  background-color: #115500;
  color: white;
  padding: 0.75rem 3rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 25px;
  font-weight: bold;
}

.button:hover,
.button:active {
  background-color: #092c00;
}

.button__icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.button__badge {
  background-color: #229c03;
  padding: 0.25rem 1rem;
  border-radius: 25px;
  margin-left: 1rem;
  font-weight: bold;
}

.bump {
  animation: bump 300ms ease-out;
}

@keyframes bump {
  0% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.9);
  }
  30% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #009e4f;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
  z-index: 10;
}

.header__wrapper {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.header__image {
  width: 110%;
  height: 100%;
  object-fit: cover;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 20vh;
  left: 5%;
  width: 90%;
  background-color: white;
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation: slide-down 300ms ease-out forwards;
}

@media (min-width: 768px) {
  .modal {
    width: 40rem;
    left: calc(50% - 20rem);
  }
}

@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #009e4f;
  padding: 1rem 0;
  margin: 1rem 0;
}

.cart-item h2 {
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.cart-item__summary {
  width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-item__price {
  font-weight: bold;
  color: #009e4f;
}

.cart-item__amount {
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  color: #363636;
}

.cart-item__actions {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .cart-item__actions {
    flex-direction: row;
  }
}

.cart-item button {
  font: inherit;
  font-weight: bold;
  font-size: 1.25rem;
  color: #009e4f;
  border: 1px solid #009e4f;
  width: 3rem;
  text-align: center;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;
  margin-left: 1rem;
  margin: 0.25rem;
}

.cart-item button:hover,
.cart-item button:active {
  background-color: #009e4f;
  color: white;
}
.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 25rem;
  overflow-y: scroll;
}

.cart__total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.cart__actions {
  text-align: right;
}

.cart__button {
  font: inherit;
  cursor: pointer;
  background-color: #009e4f;
  color: white;
  border: 1px solid #009e4f;
  padding: 0.5rem 2rem;
  border-radius: 25px;
  margin-left: 1rem;
}

.cart__button:hover,
.cart__button:active {
  background-color: #197446;
  border-color: #197446;
  color: white;
}

.cart__button_alt {
  background-color: white;
  color: #009e4f;
}

